@use "../../styles/mixin_font.scss" as *;
@use "../../styles/mixin_adaptiv.scss" as *;
.desctop{
  display: none;
  @include respond(desktop) {
    display:block;
  }   
}

.filterWrap{
  position: absolute;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 16px;
  background-color:var(--color-bg-main); 
  left: 0;  
  top:0; 
  @include respond(desktop) {
    justify-content:space-between;  
    padding: 16px 40px; 
  }   
}
.inputWrap {
  position: relative;
}

.searchButton {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 36px;
  height: 36px;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  display: block;
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.12s ease, transform 0.12s ease;
  pointer-events: none; 
}


.iconDefault { opacity: 1; }
.iconActive  { opacity: 0; }

.inputWrap:focus-within .iconDefault {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.95);
}
.inputWrap:focus-within .iconActive {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}


.input {
  border: 1px solid var(--color-header-border);
  background-color: var(--color-bg-main);
  border-radius: 24px;
  padding: 8px 12px 8px 48px; 
  width: 243px;
  height: 40px;
  outline: none;
  transition: border-color 0.2s ease;
}

.clearButton {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-input-search);
  font-size: 18px;
  font-weight: bold;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.clearButton:hover {
  background-color: var(--color-header-border);
  color: var(--color-text);
}


.inputWrap:focus-within .input {
  border-color: var(--color-text-active);
}
.inputWrap:hover .input {
  border-color: var(--color-text-active);
}

.input::placeholder {
  color: var(--color-input-search);
  @include caption;  
}



.btnFilter,.btnGeo{
  width: 40px;
  height: 40px;
  border: 1px solid var(--color-input-search);
  border-radius: 24px;
  padding: 8px;    
}

.btnFilter, .btnGeo{
  width: 40px;
  height: 40px;
  background-color: var(--color-bg-main);
  border: 1px solid var(--color-header-border);
  border-radius: 14px;
  padding: 8px;
  display: flex;   
  justify-content: center; 
  align-items: center;
}

.btnFilter{
  @include respond(desktop) { 
    position: absolute;
    top: 130px; 
    left: 20%;   
    width: 135px;
    display: flex;
    gap:10px;
    justify-content: center;
    align-items: center;
  }   
}
.btnFilterText{
  display: none;
   @include respond(desktop) {
    display: block;
   }
}
.iconGeo, .iconFilter {
  width: 24px;
  height: 24px;
  transition: opacity .15s ease, transform .15s ease;
}

.iconGeoHover, .iconFilterHover { 
  display: none;
  opacity: 0; 
}

.btnFilter:hover .iconFilterDefault,.btnGeo:hover .iconGeoDefault { 
  display: none; 
  opacity: 0; 
}

.btnFilter:hover .iconFilterHover, .btnGeo:hover .iconGeoHover  { 
  display: inline-block;
  opacity: 1;   
}
.geoMessage {
  position: absolute;
  margin-top: 10px;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  z-index: 1000;
}

.success {
  background-color: var(--color-bg-main);
  border: 1px solid var(--color-input-active);
  color:var(--color-btn-disabled);
}

.error {
  background-color: var(--color-text-btn-active);
  border: 1px solid var(--color-bg-ad);
  color: var(--color-filter-btn);
}

.closeBtn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: inherit;
  padding: 0;
}
.btnReg{
  @include button;
    padding:10px 33px;   
    border-radius: 24px;
    list-style: none;    
    text-align:center;
    text-decoration: none;
    background-color: var(--color-text-active); 
    color:var( --color-text-btn-active);
}
.btnReg:hover{
    cursor: pointer;   
    opacity: 0.8; 
}
.flex{
  display: flex;
  text-align: center;
  align-items: center;
  gap:36px;
}