.Backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow-y: scroll;
}

.Overlay {
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.Modal {
    position: absolute;
    top: 30%;
    left: 50%;
}

.ModalFilters{
    position: absolute;
    z-index: 1000; 
}

.ModalNotification {   /*integrate into logic*/
    position: fixed; /* instead of absolute */
    bottom: 0;
    left: 0;
    width: 100vw;
    z-index: 1000;
}

